<template lang="pug">
.comments(:class='{ open: isOpenComments, "comments--admin": admin }')
    h4.comments__title
        span {{ $t("title") }} ({{ info.total }})
        a.comments__show(@click.prevent='showComments', href='#', v-if='info.data.length > 1') {{ showText }}
    .comments__list(v-if='getInfo')
        comment-block(
            :admin='admin',
            v-for='i in info.data',
            :key='i.id',
            :info='i',
            :edit='getInfo.id === i.author_id',
            :deleted='getInfo.id === i.author_id',
            @edit-comment='onEditMain'
        )
        .div(v-if='isOpenComments && commentOffset + commentPerPage <= info.total') 
            button.btn-load-comments-text(type='button', v-if='!isLoad', @click.prevent='loadComments') Показать следующие комментарии
            .btn-load-comments-container(v-else)
                span.btn-load-comments-container_icon
                span.btn-load-comments-container_icon
                span.btn-load-comments-container_icon
        .comments__add(v-if='!admin')
            comment-add(ref='addComment', :id='id', v-model='commentText', @submited='onSubmitComment')
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import CommentBlock from '@/components/Comments/Block'
import CommentAdd from '@/components/Comments/Add'
export default {
    name: 'Comments',
    props: {
        admin: Boolean,
        info: Object,
        id: Number,
        edit: Boolean,
        deleted: Boolean,
    },
    components: { CommentBlock, CommentAdd },
    data: () => ({
        isOpenComments: false,
        commentText: '',
        commentEdit: false,
        commentEditInfo: null,
        isLoad: false,
        commentOffset: 0,
        commentPerPage: 5,
    }),
    computed: {
        ...mapGetters('profile/info', ['getInfo']),
        showText() {
            if (localStorage.getItem('lang') === 'en') {
                return this.isOpenComments ? 'hide' : 'show'
            }
            return this.isOpenComments ? 'скрыть' : 'показать'
        },
    },
    methods: {
        ...mapActions('profile/comments', ['commentActions']),
        ...mapActions('profile/comments', ['addCommentsById']),
        showComments() {
            this.isOpenComments = !this.isOpenComments
        },
        onEditMain({ commentInfo, commentText }) {
            this.commentEdit = true
            this.commentText = commentText
            this.commentEditInfo = commentInfo
            this.$refs.addComment.$refs.addInput.focus()
        },
        onSubmitComment() {
            if (this.commentText === '') return
            this.commentActions({
                edit: this.commentEdit,
                post_id: this.id,
                text: this.commentText,
                id: this.commentEdit ? this.commentEditInfo.id : null,
                parent_id: null,
            }).then(() => {
                this.commentText = ''
                this.commentEdit = false
                this.commentEditInfo = null
            })
        },
        loadComments() {
            if (this.commentOffset < this.info.total) {
                this.isLoad = true
                const data = {
                    post_id: this.id,
                    offset: this.commentOffset + this.commentPerPage,
                    perPage: this.commentPerPage,
                }
                this.addCommentsById(data).then(() => {
                    this.commentOffset += this.commentPerPage
                    this.isLoad = false
                })
            }
        },
    },
    i18n: {
        messages: {
            en: {
                title: 'Comments',
            },
            ru: {
                title: 'Комментарии',
            },
        },
    },
}
</script>

<style lang="stylus">
@import '../../assets/stylus/base/vars.styl';

.comments {
    .comment-block:not(:first-child) {
        display: none;
    }

    &.open {
        .comment-block, .comment-block__reviews {
            display: block;
        }
    }

    &.comments--admin {
        padding-bottom: 40px;

        .comment-block {
            &:first-child {
                display: none;
            }

            .edit {
                display: none;
            }
        }

        &.open {
            .comment-block:first-child {
                display: block;
            }
        }
    }
}

.comments__title {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    span {
        font-family: font-exo;
        font-weight: bold;
        font-size: 15px;
        color: #000;
        display: block;
        margin-right: 5px;
    }
}

.comments__show {
    font-size: 13px;
    color: eucalypt;
}

.comments__list {
    width: 100%;
    max-width: 580px;
}

.btn-load-comments-text {
    color: #21a45d;
    font-size: 13px;
    font-weight: 600;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: transparent;
    cursor: pointer;
}

.btn-load-comments-container {
    display: grid;
    grid-template-columns: repeat(3, 10px);
    grid-column-gap: 4px;

    &_icon {
        display: block;
        width: 100%;
        height: 5px;
        background-color: #21a45d;
        opacity: 0.3;
        transition: opacity 0.3s;
        animation-name: loadAnimations;
        animation-duration: 3s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;

        &:nth-child(1) {
            -webkit-animation-delay: 0.5s;
        }

        &:nth-child(2) {
            -webkit-animation-delay: 1s;
        }

        &:nth-child(3) {
            -webkit-animation-delay: 1.5s;
        }
    }
}

@keyframes loadAnimations {
    0% {
        opacity: 0.3;
        transition: opacity 0.3s;
    }

    66% {
        opacity: 1;
        transition: opacity 0.3s;
    }
}
</style>



